.c-card {
  display: block;
  border: 1px solid rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
}

.c-card__img {
  aspect-ratio: 345/220;
  object-fit: cover;
  overflow: hidden;
}

.c-card__body {
  padding: 1.5625rem 1.25rem;
  flex-grow: 1;
}

.c-card__head {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.5;
}

.c-card__text {
  color: rgba(0, 0, 0, 0.8);
  font-size: 1.125rem;
  line-height: 1.667;
  margin-top: 1.5625rem;
  flex-grow: 1;
}

.c-section-title {
  display: inline-block;
  font-size: 2.25rem;
  font-weight: 700;
  line-height: 1.5;
  color: #eb6100;
}
@media screen and (min-width: 768px) {
  .c-section-title {
    font-size: 3rem;
    line-height: 1.5;
  }
}

